<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/logo.png">
    <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <!-- <HomePage /> -->
    <!-- <router-view /> -->
    <!-- 缓存的页面，缓存$route.meta.keepAlive为true的组件 -->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <!-- 不缓存的页面，不缓存$route.meta.keepAlive为false的组件 -->
    <router-view v-if="!$route.meta.keepAlive"></router-view>
    <van-tabbar route>
      <van-tabbar-item replace to="/home" icon="home-o" @click="onClickHome"
        >首页</van-tabbar-item
      >
      <van-tabbar-item
        replace
        to="/history"
        icon="chart-trending-o"
        @click="onClickHistory"
        >历史数据</van-tabbar-item
      >
      <van-tabbar-item
        replace
        to="/control"
        icon="setting-o"
        @click="onClickControl"
        >控制</van-tabbar-item
      >
      <van-tabbar-item replace to="/mypage" icon="manager-o"
        >我的</van-tabbar-item
      >
    </van-tabbar>
  </div>
</template>

<script>
// import router from './router'
// import HelloWorld from './components/HelloWorld.vue'
import HomePage from "./views/home/index.vue";
import { getLimitOxygen } from './api/index'

export default {
  name: "App",
  components: {
    // HelloWorld,
    HomePage,
  },
  data() {
    return {
      active: "home",
    };
  },
  mounted() {
    this.getOxygenLimitData();
    const UAParser = require("ua-parser-js");
    const ua = navigator.userAgent;
    var uaParser = new UAParser(ua);
    console.log(uaParser.getResult());
    // console.log(uaParser.getDevice());
    this.$store.commit('setDeviceInfo', uaParser.getResult())
    
  },
  methods: {
    onClickHome() {
      this.$router.push({ path: "/home" });
    },
    onClickHistory() {
      this.$router.push({ path: "/history" });
    },
    onClickControl() {
      this.$router.push({ path: "/control" });
    },
    onClickMy() {
      this.$router.push({ path: "/history" });
    },
    getOxygenLimitData() {
      new Promise((resolve, reject) => {
        getLimitOxygen()
          .then((response)=>{
            // console.log(response)
            const { limit_oxygen } = response
            // console.log(limit_oxygen)
            this.$store.commit('setOxygenLimit', {oxygenData: limit_oxygen})
          })
      })
    }
  },
};
</script>

