<template>
  <div class="login-container">
    <van-nav-bar title="首页--实时数据"> </van-nav-bar>
    <van-overlay :show="showPic" @click="showPic = false">
      <van-image-preview
        v-model="showPic"
        :images="[require('@/assets/standardOxygen.png')]"
      />
    </van-overlay>
    <van-cell-group inset border>
      <!-- <van-cell title="溶氧：" center size="small" /> -->
      <van-row border>
        <van-col span="12">
          <van-field
            v-model="oxygenData"
            label="溶氧值："
            label-width="4.2em"
            label-align="right"
            placeholder="0"
            input-align="center"
            readonly
            border
          />
        </van-col>
        <van-col span="12">
          <van-field
            v-model="pressures"
            label="大气压："
            placeholder="0"
            label-align="left"
            input-align="center"
            readonly
            border
          />
        </van-col>
      </van-row>
      <van-row>
        <van-col span="12">
          <van-field
            v-model="oxygenPrec"
            label="溶氧比："
            label-width="4.2em"
            label-align="right"
            input-align="center"
            placeholder="0"
            right-icon="warning-o"
            @click-right-icon="showPic = true"
            readonly
          />
        </van-col>
        <van-col span="12">
          <van-field
            v-model="tempratureAir"
            label-align="left"
            label="空气温度："
            placeholder="0"
            input-align="center"
            readonly
          />
        </van-col>
      </van-row>
      <van-row>
        <van-col span="12">
          <van-field
            v-model="temprature"
            label="水温："
            label-width="3.2em"
            label-align="right"
            placeholder="0"
            input-align="center"
            readonly
          />
        </van-col>
      </van-row>
      <!-- <van-cell title="温度：" :value="temprature" label="描述信息" /> -->
    </van-cell-group>
    <van-progress
      :percentage="currentPercent"
      :pivot-text="readStatus"
      pivot-color="#7232dd"
      color="linear-gradient(to right, #be99ff, #7232dd)"
    />
    <LineChart
      v-on:temperChange="updateTemp"
      :testData11="testresponse"
      :temperData="tempratureData"
      :pressureData="tpressureData"
      :height="calculateHeight"
    />
    <!-- <van-tabbar v-model="active">
      <van-tabbar-item icon="home-o">首页</van-tabbar-item>
      <van-tabbar-item icon="chart-trending-o" to:>历史数据</van-tabbar-item>
      <van-tabbar-item icon="setting-o">控制</van-tabbar-item>
      <van-tabbar-item icon="manager-o">我的</van-tabbar-item>
    </van-tabbar> -->
  </div>
</template>

<script>
import { Locale } from "vant";
import LineChart from "./components/LineChart";
import { env, number } from "echarts";

export default {
  name: "HomePage",
  components: {
    LineChart,
  },
  data() {
    return {
      ischecked: false,
      showPic: false,
      pictList: ["@/assets/standardOxygen.png"],
      active: 0,
      oxygenData: "",
      temprature: "",
      oxygenPrec: "",
      pressures: 0,
      tempratureAir: 0,
      testresponse: [],
      tempratureData: [],
      tpressureData: [],
      currentPercent: 0,
      percentTimer: null,
      readStatus: "",
      ws: null,
    };
  },
  computed: {
    calculateHeight: function () {
      let chartheight = window.screen.height - 46 - 48 - 50 - 40 - 200;
      // let chartheight = document.body.clientHeight - 46 - 48 - 50 - 40;
      console.log('clientHeight:',document.body.clientHeight)
      console.log('screenHeight:',window.screen.height)
      console.log('offsetHeight:',document.body.offsetHeight)
      console.log('scrollHeight:',document.body.scrollHeight)
      console.log('availHeight:',window.screen.availHeight)
      console.log('screen:',window.screen)
      return `${chartheight}px`;
    },
  },
  watch: {
    $route: {
      handler: function (route) {
        this.redirect = route.query && route.query.redirect;
      },
      immediate: true,
    },
  },
  mounted() {
    // this.openWebSocket();
    this.createWebsocket();
    this.percentTimer = setInterval(() => {
      if (this.currentPercent < 90) {
        this.currentPercent = this.currentPercent + 0.5;
        this.readStatus = "读取中……";
      } else if (this.currentPercent < 100) {
        this.currentPercent = Math.round((this.currentPercent + 0.1) * 10) / 10;
      }
    }, 25);
  },
  beforeDestroy() {
    clearInterval(this.percentTimer);
  },
  methods: {
    showPwd() {
      if (this.passwordType === "password") {
        this.passwordType = "";
      } else {
        this.passwordType = "password";
      }
      this.$nextTick(() => {
        this.$refs.password.focus();
      });
    },
    updateTemp(e) {
      this.temprature = e;
    },
    updateOxygen(e) {
      this.oxygenData = e;
    },
    createWebsocket() {
      let base_url = location.host;
      let currentEnv = process.env.NODE_ENV;
      // console.log('currentEnv:',currentEnv)
      let websocketUrl =
        currentEnv === "development"
          ? "ws://127.0.0.1:8002/ws"
          : `ws://${base_url}/ws`;
      // let websocketUrl = `ws://${base_url}/ws`; //product
      // let websocketUrl = "ws://127.0.0.1:8002/ws"; //dev
      console.log("websocketUrl:", websocketUrl);
      if ("WebSocket" in window) {
        console.log("您的浏览器支持WebSocket");
      } else {
        console.log("您的浏览器不支持WebSocket");
        return;
      }
      this.ws = new WebSocket(websocketUrl);
      this.initWebSocket();
    },
    wsReconnect() {
      setTimeout(() => {
        console.log("ws reconnect");
        this.createWebsocket();
        // this.initWebSocket();
      }, 5000);
    },
    initWebSocket() {
      let that = this;
      // create an websocket instance

      that.ws.onopen = function (e) {
        // heartCheck.reset().start();      //心跳检测重置
        console.log(new Date());
        console.log("websocket 已连接");
        // console.log(websocketUrl);
      };
      that.ws.onerror = function (e) {
        console.log(e);
      };
      that.ws.onclose = function (e) {
        console.log(new Date());
        console.log("websocket 断开连接！");
        that.wsReconnect();
      };
      that.ws.onmessage = function (event) {
        // that.currentPercent = 100;
        // that.readStatus = "读取完成";
        console.log("event_log:", event);
        console.log(event.data);
        let data = event.data;
        let datajson = JSON.parse(data);
        that.oxygenData = `${datajson.oxygen} mg/L`;
        that.temprature = `${datajson.temper} ℃`;
        that.oxygenPrec = `${datajson.oxygen_perc} %`;
        that.tempratureAir = `${datajson.temp_air} ℃`;
        that.pressures = `${datajson.pressure} Pa`;
        let dateTime = datajson.date_time.replace(/-/g, "/");
        while (that.testresponse.length > 10) {
          that.testresponse.shift();
          that.tempratureData.shift();
          that.tpressureData.shift();
        }
        // console.log('datetime:', dateTime)
        that.testresponse.push([dateTime, datajson.oxygen]);
        that.tempratureData.push([dateTime, datajson.temper]);
        that.tpressureData.push([dateTime, datajson.pressure]);
        that.currentPercent = 0;
        that.readStatus = "读取中……";
        //心跳保活
        // heartCheck.reset().start();      //拿到任何消息都说明当前连接是正常的
        // if(event.data!="pong"){
        //   let data = event.data;
        //   console.log(data)
        //   console.log(typeof(data))
        //   let datajson = JSON.parse(data)
        //   console.log(datajson)
        //   console.log(typeof(datajson))
        //   that.testresponse.push([datajson.date_time,datajson.oxygen])
        // }
      };
      window.onbeforeunload = function () {
        that.ws.close();
      };

      //心跳检测
      // var heartCheck = {
      //     timeout: 1000,        //1分钟发一次心跳
      //     timeoutObj: null,
      //     serverTimeoutObj: null,
      //     reset: function(){
      //         clearTimeout(this.timeoutObj);
      //         clearTimeout(this.serverTimeoutObj);
      //         return this;
      //     },
      //     start: function(){
      //         var self = this;
      //         this.timeoutObj = setTimeout(function(){
      //             //这里发送一个心跳，后端收到后，返回一个心跳消息，
      //             //onmessage拿到返回的心跳就说明连接正常
      //             ws.send("ping");
      //             console.log("ping!")
      //             self.serverTimeoutObj = setTimeout(function(){//如果超过一定时间还没重置，说明后端主动断开了
      //                 ws.close();     //如果onclose会执行reconnect，我们执行ws.close()就行了.如果直接执行reconnect 会触发onclose导致重连两次
      //             }, self.timeout)
      //         }, this.timeout)
      //     }
      // }
    },
  },
};
</script>

<style lang="scss" scoped>
$bg: #2d3a4b;
$dark_gray: #889aa4;
$light_gray: #eee;

.login-container {
  min-height: 100%;
  width: 100%;
  background-color: $bg;
  overflow: hidden;

  .img-div-container {
    position: relative;
    .standardImg {
      width: 100%;
      height: 100%;
    }
  }
}
</style>
