import request from '@/utils/request'

export function getDataHours(hours) {
  return request({
    url: '/api/fish/getOxygenOneHour',
    method: 'get',
    params: { hours }
  })
}

export function getDataDays(startDay, endDay) {
  return request({
    url: '/api/fish/getOxygendays',
    method: 'get',
    params: { startDay, endDay }
  })
}

export function getLimitOxygen() {
  return request({
    url: '/api/wechat/getOxygenLimit',
    method: 'get'
  })
}

export function setLimitOxygen(data) {
  return request({
    url: '/api/wechat/setOxygenLimit',
    method: 'post',
    data
  })
}
export function getLimitOxygenRecord() {
  return request({
    url: '/api/wechat/getRecordOxygenLimit',
    method: 'get'
  })
}
export function getWarningOxygenRecord() {
  return request({
    url: '/api/wechat/getRecordOxygenWarning',
    method: 'get'
  })
}

export function getInfo(token) {
  return request({
    url: '/vue-admin-template/user/info',
    method: 'get',
    params: { token }
  })
}

export function logout() {
  return request({
    url: '/vue-admin-template/user/logout',
    method: 'post'
  })
}
