import Vue from 'vue'
import Vuex from 'vuex'



Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        oxygenLimit: 5,
        deviceInfo: null
    },
    mutations: {
        setOxygenLimit(state, playload) {
            state.oxygenLimit = playload.oxygenData
        },
        setDeviceInfo(state, playload) {
            // state.deviceInfo = playload.model
            state.deviceInfo = `${playload.device.vendor} ${playload.device.model}(${playload.os.name} ${playload.os.version})`
        },
    },
    actions: {
        setOxygenLimitAsync({ commit }, playload) {
            setTimeout(() => {
                commit('setOxygenLimit', playload);
            }, 1000);
        }
    }
})

export default store